import React from "react";
import { Link, Outlet } from "react-router-dom";

import { APP_ROUTES } from "../../constants/app";
import { getLastPart, pluralize } from "../../helpers/string";
import timeIcon from "../../images/calendar-time.svg";
import lockIcon from "../../images/lock.svg";
import "./styles.scss";

export default function Tools() {
  return (
    <div className="Tools">
      <main className="h-full overflow-y-auto">
        <div className="container px-6 mx-auto grid">
          <div className="grid gap-5 sm:gap-10 mb-8 md:grid-cols-2 xl:grid-cols-4">
            {getToolsListMarkup()}
          </div>
        </div>
      </main>
      {/* Outlet for child routes */}
      <Outlet />
    </div>
  );
}

function getToolsListMarkup() {
  const markup: JSX.Element[] = [];

  toolsListMap.forEach((item, key) => {
    markup.push(
      <Link key={item.name} to={key}>
        <div className="card">
          <div className="cardIcon">
            <img className="cardIconImg" src={item.icon} alt={item.name} />
          </div>
          <div>
            <p className="cardHead">{item.name}</p>
            <p className="cardSubHead">
              {item.noOfTools} {pluralize("tool", item.noOfTools)}
            </p>
          </div>
        </div>
      </Link>
    );
  });

  return markup;
}

const toolsListMap = new Map([
  [
    APP_ROUTES.TOOLS_CRYPTO,
    {
      name: getLastPart(APP_ROUTES.TOOLS_CRYPTO, "/"),
      icon: lockIcon,
      noOfTools: 1
    }
  ],
  [
    APP_ROUTES.TOOLS_TIME,
    {
      name: getLastPart(APP_ROUTES.TOOLS_TIME, "/"),
      icon: timeIcon,
      noOfTools: 0
    }
  ]
]);
